/* ------------------------------
   Status Card
------------------------------ */
const StatusCard = ({ statusSteps, currentStep, handleNext, handlePrev, currentLabel }: any) => (
  <div className="status-card">
    <div className="status-header">
      <h3 style={{ margin: 0, fontSize: "15px", fontWeight: 600 }}>Status Progress</h3>
      <span style={{ fontSize: "13px", color: "var(--muted)" }}>
        Current: <b style={{ color: "var(--accent)" }}>{currentLabel}</b>
      </span>
    </div>

    <div className="status-steps">
      {statusSteps.map((s: any) => {
        const done = s.step < currentStep;
        const active = s.step === currentStep;
        return (
          <div key={s.step} className="status-step">
            <div className={`status-circle ${done ? "done" : active ? "active" : ""}`}>
              {done ? <CheckCircle2 size={14} /> : s.step}
            </div>
            <div className={`status-label ${active ? "active" : ""}`}>{s.label}</div>
          </div>
        );
      })}
    </div>

    <div className="status-controls" style={{ borderTop: `1px solid var(--border)`, paddingTop: 20, marginTop: 20 }}>
      <button onClick={handlePrev} disabled={currentStep === 1} style={{ opacity: currentStep === 1 ? 0.5 : 1 }}>
        <ArrowLeft size={14} /> Back
      </button>
      <button
        onClick={handleNext}
        disabled={currentStep === statusSteps.length}
        className="primary-btn"
        style={{
          opacity: currentStep === statusSteps.length ? 0.6 : 1,
          cursor: currentStep === statusSteps.length ? "not-allowed" : "pointer",
        }}
      >
        Next <ArrowRight size={14} />
      </button>
    </div>
  </div>
);
/* ------------------------------
   Messages Panel
------------------------------ */
const MessagesPanel = ({ messages, markAsSeen }: any) => {
  const handleNewMessage = () => {
    navigate("message"); // ✅ relative path → stays under current route
  };

  const handleOpenMessage = (id: string | number) => {
    navigate(`message/${id}`); // ✅ appends message id to current route
  };

  return (
    <div className="messages-panel">
      <div className="messages-header">
        <h3>Messages</h3>
        <button className="primary-btn" style={{ fontSize: "12px" }} onClick={handleNewMessage}>
          New Message
        </button>
      </div>

      {messages.length === 0 ? (
        <div className="messages-empty">
          No messages yet — this user hasn’t received or sent any messages.
        </div>
      ) : (
        <div style={{ display: "flex", flexDirection: "column", gap: "8px" }}>
          {messages.map((msg: any) => (
            <div
              key={msg.id}
              onClick={() => {
                markAsSeen(msg.id);
                handleOpenMessage(msg.id); // ✅ open message view
              }}
              style={{
                cursor: "pointer",
                background: msg.unseen ? "var(--accent-bg)" : "transparent",
                fontWeight: msg.unseen ? 600 : 400,
                padding: "10px 12px",
                borderRadius: "8px",
                display: "grid",
                gridTemplateColumns: "1fr auto 50px",
                alignItems: "center",
                transition: "background 0.2s",
              }}
            >
              <div>{msg.text.length > 60 ? msg.text.slice(0, 60) + "..." : msg.text}</div>
              <div style={{ color: "var(--muted)", fontSize: "13px" }}>{formatDate(msg.datetime)}</div>
              <div
                style={{
                  textAlign: "right",
                  color: msg.attachments.length ? "var(--accent)" : "var(--muted)",
                }}
              >
                {msg.attachments.length > 0 ? msg.attachments.length : "—"}
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

/* ------------------------------
   Save Button
------------------------------ */
const SaveCard = ({ saving, handleSave }: any) => (
  <div className="save-card">
    <button
      className="primary-btn"
      onClick={handleSave}
      disabled={saving}
      style={{
        opacity: saving ? 0.8 : 1,
        cursor: saving ? "not-allowed" : "pointer",
        display: "inline-flex",
        alignItems: "center",
        justifyContent: "center",
        gap: "6px",
        minWidth: "130px",
      }}
    >
      {saving ? (
        <>
          <span
            style={{
              width: "14px",
              height: "14px",
              border: "2px solid rgba(255,255,255,0.4)",
              borderTopColor: "white",
              borderRadius: "50%",
              display: "inline-block",
              animation: "spin 0.8s linear infinite",
            }}
          ></span>
          Saving...
        </>
      ) : (
        "Save Changes"
      )}
    </button>
  </div>
);

